<template>
  <div class="views" id="views">
    <div id="container" class="container"></div>
  </div>
</template>

<script>
import { App } from '@/assets/App/App'

const app = new App()
export default {
  data () {
    return {
      params: {
        methods: {
          animation: true, // 循环调用渲染
          renderer: true, // 应用渲染器
          light: true, // 在场景中加入全景光
          controls: true, // 使用轨道控制器，鼠标滚轮控制放大缩小，左键拖动绕中心点旋转，右键拖动中心点。
          statement: true // 在控制台打印声明，与本案例无关，可忽略。
        },
        events: {
          windowResize: true // 监听窗口尺寸变化，并改变渲染页面尺寸
        },
        params: {
          container: null, // 渲染结果输出dom页面
          renderer: [
            'WebGLRenderer'
          ],
          controls: [
            'OrbitControls'
          ],
          rendererParams: {
            antialias: true // 抗锯齿，可忽略。
          }
        }
      }
    }
  },
  mounted () {
    this.params.params.container = document.getElementById('container') // 获取渲染结果输出dom页面
    /**
     * 初始化场景
     */
    app.init(this.params, (scene, camera, renderer) => {
      camera.position.set(100, 100, 100) // 修改相机位置
    })
    /**
     * 导入模型摄像头
     */
    app.addModel({
      url: 'static/model/fbx/cqcet/摄像头.FBX', // 模型地址
      type: 'fbx' // 模型格式
    }, (obj) => { // 加载完成回调，可忽略。
      console.log('模型摄像头加载完成')

      obj.position.set(-10, -10, -10) // 修改摄像头模型的坐标，使其偏离原点

      this.loadOver(obj) // 模型加载完后调用
    })
    /**
     * 导入模型冰箱
     */
    app.addModel({
      url: 'static/model/fbx/cqcet/冰箱.FBX', // 模型地址
      type: 'fbx' // 模型格式
    }, (obj) => { // 加载完成回调，可忽略。
      console.log('模型冰箱加载完成')

      obj.scale.set(0.1, 0.1, 0.1) // 修改模型缩放比例，由于冰箱模型体型偏大，需要缩小到原来的0.1倍
      obj.position.set(10, 10, 10) // 修改空调模型的坐标，使其偏离原点

      this.loadOver(obj) // 模型加载完后调用
    })
  },
  methods: {
    loadOver (obj) {
      if (!this.obj) { // 第一次被调用时执行
        this.obj = obj
      } else { // 第二次被调用时执行（此时两模型均已加载完），获取两模型间距离并打印到控制台
        console.log('两模型之间的距离为：', app.getObjDistance(this.obj, obj))
      }
    }
  }
}
</script>

<style scoped>
.views {
  height: 100%;
  width: 100%;
}

.container {
  height: 100%;
  width: 100%;
}
</style>
